// @file _variables.scss

// Primary colors
$dart-green: #00693e;
$forest-green: #12312b;
$spring-green: #a5d75f;
$white: #fff;
$black: #000;
$off-white: #f5f5f5;
$summer-yellow: #f5dc69;
$light-blue: #9bc8eb;
$light-dart-green: #ecf3f0;

// Grays
$gray-intro: #616161;
$gray: $gray-intro;
$gray-dark: #333;
$gray-medium: #ccc;
$college-background-gray: #e2e2e2;
$college-form-gray: #f7f7f7;
$gray-line-deco: #9e9e9e; // only for decoration as it fails WCAG for contrast against white

// Accent colors
$green-accent: #e9eee0; // pre-Drupal library green
$gold-accent: #e8b00c;
$red: #9d162e;
$blue-light: #9bc8eb; // light blue that contrasts well on $dart-green WCAG AA
$yellow: #f5dc69; // a11y, link outline when focus and hover in footer
$bright-red: #f00;
$cream: #fffdf7;
$bsa-outline-color: #ffa00f;

// blues
$teal: #f5fbfa; // for input backgrounds
$blue: #003c73; // #337ab7;
$aqua: #54cee0; // used by the College
$river-blue: $blue; // #267aba;

// hours colors
$hours-light-blue: $light-blue;
$hours-light-green: $spring-green;
$hours-light-yellow: $yellow;

// Highlight colors for view blocks
$light-red: lighten($red, 40%);
$light-yellow: lighten($yellow, 20%);
$light-green: lighten($dart-green, 30%);
$library-brand-color: $dart-green;
$jones-brand-color: $yellow;

// Links.
$link-color: $dart-green;
$link-hover: $dart-green; // lighten($link-color, 10%);
$link-visited: $dart-green;
$link-focus: $link-hover;
$link-active: $link-focus;
$link-hover-feedback: $blue-light; // Hover link color for the site feedback link.

// Typography
// -------------
$font-family-serif-base: "Times New Roman", georgia, garamond, serif;
$font-family-base: verdana, geneva, sans-serif;
$font-family-sans-serif: "National2Web", $font-family-base;
$font-family-serif: "DartmouthRuzicka", $font-family-serif-base;
$headings-font-family-h1: "ManukaBold", $font-family-base;
$headings-font-family-others: "National2Web", $font-family-base;
$font-weight-strong: 700;

// Override variables in the bootstrap/stylesheets/bootstrap folder
// in relationship to the generated folder
$font-awesome: var(--fa-font-brands);
$material-symbols: "Material Symbols Outlined";
$font-primary-icons: $material-symbols;
$base-body-font-size: 18px;
$base-font-size: 18px;
$base-line-height: normal;

// Full-width page adjustment
$max-text-width: 930px;
$component-spacing-bottom: $base-font-size * 2;
