// Set the variables for use in stylesheet

// Colors
$black: #000;
$forest-green: #2d4821;
$blue-dark: #214c62;
$blue-light: #b3d9ff;
$red-dark: #6c4a00;
$red: #d2322d;
$yellow: #f9f1c6;

// Outline
$outline-default-style: solid;
$outline-default-color: #ffa00f;
$outline-default-offset: -2px;

// Alerts Colors
$alert-success-color: $forest-green;
$alert-info-color: $blue-dark;
$alert-warning-color: $red-dark;
$alert-warning-bkg: $yellow;
$alert-danger-color: $red;
$alert-danger-hover: darken($red, 10%);
